html {
  height: 100%; }

body {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  height: 100%;
  min-width: 320px;
  margin: 0;
  padding: 0;
  font-family: "Fira Sans", "Arial", sans-serif;
  background-color: mediumaquamarine; }

img {
  max-width: 100%; }

a {
  text-decoration: none; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
          clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.br-tablet,
.br-mobile {
  display: none; }

.br-desktop {
  display: inline; }

@media (max-width: 1023px) {
  .br-mobile,
  .br-desktop {
    display: none; }
  .br-tablet {
    display: inline; } }

@media (max-width: 767px) {
  .br-desktop,
  .br-tablet {
    display: none; }
  .br-mobile {
    display: inline; } }
